<template>
	<div class="card has-background-light">
		<div class="card-image">
			<figure class="image">
				<img :src="require('@/games/'+nome+'/header.jpg')"/>
			</figure>
		</div>
		<div class="card-content">
			<div class="media">
				<div class="media-content">
					<div v-if="game.title" class="title is-2">{{game.title}}</div>
					<!--<div class="subtitle is-6">shmup, runner</div>-->
					<div v-if="game.release" class="subtitle is-6">Release: {{game.release}}</div>
					<div class="subtitle is-6">{{game.worked_as}}</div>
					<b-taglist>
						<b-tag v-if="game.genres" v-for="tag in game.genres" type="is-primary">{{tag}}</b-tag>
					</b-taglist>
				</div>
			</div>

			<div v-html="description" class="content has-text-left">
			</div>
		</div>
		<div class="card-footer">
			<a v-for="link in game.links" :href="link.link" :class="'button card-footer-item ' + link.color">
				<b-icon :pack="link.pack" :icon="link.icon"></b-icon>
				<span>{{link.title}}</span>
			</a>
		</div>
	</div>
</template>

<script type="text/javascript">
	export default {
		props: {
			nome: String
		},
		data() {
			let json = require('@/games/'+this.nome+'/index.json')
			let description = require('raw-loader!../games/'+this.nome+'/description.html')
			return {
				game: json,
				description: description
			}
		},
		watch: {
			nome: function(val) {
				let json = require('@/games/'+this.nome+'/index.json');
				let description = require('raw-loader!../games/'+this.nome+'/description.html');
				this.game = json;
				this.description = description;
			}
		}
	}
</script>

